import React, { Component } from "react";
import "./style.css";

export default class ProgressBar extends Component {
  render() {
    const { value } = this.props;
    return (
      <div className="progress">
        <div className="progress-bar" style={{ width: `${value}%` }} />
        <span>{value}%</span>
      </div>
    );
  }
}
